.img-header {
  width: 100%;
  height: 100%;
  -o-object-fit: fill;
     object-fit: fill;
}
.header {
  height: 170px;
}
.title {
  color: var(--white);
  font-size: 30px;
  z-index: 9999;
  -webkit-transform: translateY(-250%);
      -ms-transform: translateY(-250%);
          transform: translateY(-250%);
  margin-left: -53%;
}

@media (min-width: 600px) {
  .header {
    height: 260px;
  }
  .title {
    font-size: 30px;
  }
  .img-header {
    -o-object-fit: cover;
       object-fit: cover;
  }
}

@media (min-width: 1000px) {
  .header {
    height: 365px;
  }
  .title {
    font-size: 40px;
  }
  .img-header {
    -o-object-fit: cover;
       object-fit: cover;
  }
}



