.container-position {
  position: absolute;
}

.blue-bag {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  left: 208px;
}

.product {
  width: 276px;
  height: 276px;
  margin: 1%;
  -webkit-box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
          box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  background: var(--white);
}

.product-container {
  width: 252px;
  height: 182px;
  border-bottom: 1.5px solid var(--grey);
}

.photo {
  max-width: 100%;
  max-height: 100%;
}

.container-description {
  width: 100%;
  -ms-flex-pack: start;
      justify-content: start;
  top: 3px;
}

.product-name {
  font-size: 16px;
  color: var(--textLightGrey);
}

.product-category {
  font-size: 16px;
  color: var(--darkLightGrey);
}

.selected-product {
  position: relative;
  width: 276px;
  height: 276px;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
  -ms-flex-align: center;
      align-items: center;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(10, 212, 250, 0.86)),
    to(rgba(37, 187, 241, 0.86))
  );
  background: -webkit-linear-gradient(
    top,
    rgba(10, 212, 250, 0.86) 0%,
    rgba(37, 187, 241, 0.86) 100%
  );
  background: -o-linear-gradient(
    top,
    rgba(10, 212, 250, 0.86) 0%,
    rgba(37, 187, 241, 0.86) 100%
  );
  background: linear-gradient(
    180deg,
    rgba(10, 212, 250, 0.86) 0%,
    rgba(37, 187, 241, 0.86) 100%
  );
}

.text-product-point {
  font-size: 36px;
  color: var(--white);
  margin-right: 10%;
}

.container-buy-white {
  position: relative;
  left: 106px;
  top: -16px;
}

.small-container {
  padding: 0.5%;
  background: white;
  margin: 0.1% 0;
}

.small-text {
  font-size: 14px;
  color: var(--textDarkGrey);
}

.h100 {
  height: 100%;
}

.container-imgHistory {
  height: 100px;
}



